.BuildControl {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 5px  0;
}

.BuildControl button {
    display: block;
    font: inherit;
    padding: 5px;
    margin: 0 5px;
    width: 80px;
    border: 1px  solid #AA6817;
    cursor: pointer;
    outline: none;
}

.BuildControl  .Less {
    background-color: #D39952;
    color: white;
}

.BuildControl  .More {
    background-color: #8f5e1e;
    color: white;
}

.BuildControl .Less:hover, .BuildControl.Less:active {
    background-color: #DAA972;
    color: white;
}